/* TrendFlow Ï†ÑÏ≤¥Î•º Í∞ÄÎ°ú Ï§ëÏïô Ï†ïÎ†¨ */
.TrendFlow {
    display: flex;
    flex-direction: column;
    align-items: center; /* ÎÇ¥Î∂Ä ÏöîÏÜå Ï§ëÏïô Ï†ïÎ†¨ */
    width: 100%;
}

/* Ïª®ÌÖåÏù¥ÎÑàÎ•º Ï§ëÏïôÏóê Ï†ïÎ†¨ÌïòÍ≥† ÏµúÎåÄ 1080pxÎ°ú Ï†úÌïú */
.container {
    text-align: center;
    padding: 20px;
    max-width: 1080px;
    width: 100%;
    margin: 0 auto; /* Í∞ÄÎ°ú Ï§ëÏïô Ï†ïÎ†¨ */
    display: flex;
    flex-direction: column;
    align-items: center; /* ÎÇ¥Î∂Ä ÏöîÏÜå Ï§ëÏïô Ï†ïÎ†¨ */
}



/* Ïó∞ÎèÑÎ≥Ñ ÏÑπÏÖò Ï§ëÏïô Ï†ïÎ†¨ */
.year-section {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    max-width: 1080px;
    width: 100%;
    margin: 0 auto;
}

.year-block {
    max-width: 1080px;
    width: 100%;
}


/* Ïó∞ÎèÑ Ï†úÎ™© (ÏôºÏ™Ω Ï†ïÎ†¨Îêú ÎîîÏûêÏù∏) */
.year-title {
    font-size: 18px;
    font-weight: bold;
    color: #026773;
    margin-bottom: 8px;
    border-left: 4px solid #026773;
    padding-left: 8px;
    text-align: left;
    width: 100%;
}

/* Î∂ÑÍ∏∞Î≥Ñ Î∞ïÏä§ Ïª®ÌÖåÏù¥ÎÑà */
.quarter-container {
    display: grid;
    grid-template-columns: repeat(4, 1fr); /* Ìïú Ï§ÑÏóê 4Í∞ú */
    gap: 15px;
    max-width: 1080px;
    width: 100%;
}

/* Í∞úÎ≥Ñ Î∂ÑÍ∏∞ Î∞ïÏä§ */
.quarter-wrapper {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
}

/* Î∂ÑÍ∏∞ ÎùºÎ≤® Ïä§ÌÉÄÏùº */
.quarter-label {
    font-size: 14px;
    font-weight: bold;
    margin-bottom: 5px;
    text-align: left;
    padding-left: 10px;
}

/* Í∞úÎ≥Ñ Î∂ÑÍ∏∞ Î∞ïÏä§ Ïä§ÌÉÄÏùº */
.quarter {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: white;
    border: 2px solid #ddd;
    border-radius: 12px;
    font-size: 16px;
    transition: all 0.3s ease-in-out;
    width: 100%;
    height: 130px;
    text-align: center;
    color: #333;
    box-shadow: 2px 4px 8px rgba(0, 0, 0, 0.1);
    cursor: pointer;
}

/* ÎßàÏö∞Ïä§Î•º Ïò¨Î†∏ÏùÑ Îïå Ìö®Í≥º */
.quarter:hover {
    background-color: #f0f8ff;
    border-color: #026773;
    transform: translateY(-3px);
    box-shadow: 4px 6px 12px rgba(0, 0, 0, 0.15);
}

/* Ï†úÎ™© Ïä§ÌÉÄÏùº */
h2 {
    font-size: 32px;
    font-weight: bold;
    margin-bottom: 20px;
}

/* Î∞òÏùëÌòï: ÌôîÎ©¥Ïù¥ ÏûëÏùÑ Îïå Î∞ïÏä§ Í∞úÏàò Ï°∞Ï†ï */
@media (max-width: 1024px) {
    .quarter-container {
        grid-template-columns: repeat(2, 1fr); /* ÌÉúÎ∏îÎ¶øÏóêÏÑúÎäî 2Í∞úÏî© */
    }
}

@media (max-width: 600px) {
    .quarter-container {
        grid-template-columns: repeat(1, 1fr); /* Î™®Î∞îÏùºÏóêÏÑúÎäî 1Í∞úÏî© */
    }
}
