  /* Í≥µÌÜµ Ïª®ÌÖåÏù¥ÎÑà Ïä§ÌÉÄÏùº */
  .container-box {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    max-width: 1400px;
  }
  
/* Ïπ¥ÌÖåÍ≥†Î¶¨ Î≤ÑÌäº Ïä§ÌÉÄÏùº */
.category-box {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: white;
    border: 2px solid #ddd;
    padding: 15px;
    border-radius: 12px;
    font-size: 16px;
    font-weight: bold;
    transition: all 0.3s ease-in-out;
    width: calc(25%);
    height: 140px;
    text-align: center;
    text-decoration: none;
    color: #333;
    box-shadow: 2px 4px 8px rgba(0, 0, 0, 0.1);
    cursor: pointer;
  }
  
  /* Ïπ¥ÌÖåÍ≥†Î¶¨ Î≤ÑÌäº Ìò∏Î≤Ñ Ìö®Í≥º */
  .category-box:hover {
    background-color: #f0f8ff;
    border-color: #026773;
    transform: translateY(-3px);
    box-shadow: 4px 6px 12px rgba(0, 0, 0, 0.15);
  }

  /* Ïπ¥ÌÖåÍ≥†Î¶¨ Ï†ïÎ†¨ */
  .category-container {
    display: flex;
    flex-direction: column;
    max-width: 1080px;
    width: 100%;
    margin: 0 auto;
    margin-bottom: 20px;
    
  }
  
  .category-title {
    font-size: 20px;
    font-weight: bold;
    color: #026773;
    margin-bottom: 8px;
    border-left: 4px solid #026773;
    padding-left: 8px;
    text-align: left;
  }
  
  /* Ïπ¥ÌÖåÍ≥†Î¶¨ Î¶¨Ïä§Ìä∏ */
  .category-list {
    display: flex;

    /* Ìïú Ï§Ñ Ï†ïÎ†¨ Ïú†ÏßÄ */
    flex-wrap: nowrap; 
    
    justify-content: center;
    /* max-width: 1080px; */
    width: 100%;
    gap: 20px;
    margin-top: 10px;
    margin-bottom: 10px;
  }
  
  /* ÌîºÏã± Í≤ΩÍ≥† Î©îÏãúÏßÄ Ï†ïÎ†¨ */
  .phishing-warning-container {
    display: flex;
