    margin-top: 10px;
    margin-bottom: 10px;
  }
  
  /* ÌîºÏã± ÏòàÏ†ú Î∞ïÏä§ Ïä§ÌÉÄÏùº */
  .phishing-box {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #fff3e0;
    border: 2px solid #f0c14b;
    /* padding: 25px; */
    border-radius: 12px;
    font-size: 18px;
    font-weight: bold;
    color: #555;
    transition: all 0.3s ease-in-out;
