.banner {
    width: 100%;
    min-height: 270px; /* ÎÜíÏù¥ ÏÇ¥Ïßù Ï¶ùÍ∞Ä */
    background-color: #f8fcfd; /* Î∞ùÍ≥† Î∂ÄÎìúÎü¨Ïö¥ ÌååÏä§ÌÖî ÌÜ§ Î∞∞Í≤Ω */
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 30px 0;
    box-sizing: border-box;
    border: 2px solid #cce7e8; /* ÏùÄÏùÄÌïú ÌÖåÎëêÎ¶¨ Ï∂îÍ∞Ä */
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.08); /* Î∂ÄÎìúÎü¨Ïö¥ Í∑∏Î¶ºÏûê */
}

.banner-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    animation: fadeIn 1s ease-in-out;
}

.logo-title {
    display: flex;
    align-items: center;
    gap: 10px;
}

.banner-logo {
    width: 55px; /* Ï†ÅÎãπÌïú ÌÅ¨Í∏∞ */
    height: auto;
}

.banner-title {
    font-size: 60px;
    color: #3CA6A6;
    font-weight: bold;
    margin: 0;
    text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.05); /* ÏïÑÏ£º Í∞ÄÎ≤ºÏö¥ Í∑∏Î¶ºÏûê */
}

.banner-subtitle {
    font-size: 22px;
    color: #444;
    margin-top: 8px;
    font-weight: 600;
}

/* Î∂ÄÎìúÎü¨Ïö¥ ÌéòÏù¥Îìú Ïù∏ Ïï†ÎãàÎ©îÏù¥ÏÖò */
@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(-5px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}
