/* Ï†ÑÏ≤¥ Ïª®ÌÖåÏù¥ÎÑà */
.ai-check-container {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
}

/* Ï†úÎ™© */
.ai-check-title {
    font-size: 28px;
    font-weight: bold;
    display: flex;
    align-items: center;
    gap: 10px;
}

/* AI Í≤ÄÏÇ¨ Ïª®ÌÖêÏ∏† Î†àÏù¥ÏïÑÏõÉ */
.ai-check-content {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    gap: 40px;
    width: 100%;
}

/* ÏûÖÎ†• Î∞è Í≤∞Í≥º ÏÑπÏÖò */
.input-section,
.result-section {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

/* ÏûÖÎ†• ÏòÅÏó≠ÏùÑ Í∞êÏã∏Îäî Ïª®ÌÖåÏù¥ÎÑà */
.input-wrapper {
    width: 90%; /* ÏûÖÎ†• Î∞ïÏä§ÏôÄ Î≤ÑÌäºÏùÑ Í∞êÏã∏Îäî ÎÑàÎπÑ */
}

/* ÏÑπÏÖò Ï†úÎ™© */
.section-title {
    font-size: 18px;
    font-weight: bold;
    color: #026773;
    margin-bottom: 10px;
    border-left: 4px solid #026773;
    padding-left: 8px;
    width: 90%; /* ÌÉÄÏù¥ÌãÄ ÎÑàÎπÑ ÎßûÏ∂§ */
    text-align: left;
}

/* ÌîºÏã± Î¨∏Ïûê ÏûÖÎ†• Î∞ïÏä§ (ÎÑàÎπÑ Ï§ÑÏûÑ) */
.input-box {
    width: 100%;
    height: 350px;
    border: 2px solid #d3d3d3;
    border-radius: 8px;
    padding: 12px;
    font-size: 16px;
    resize: none;
    background-color: white;
    box-sizing: border-box;
}

/* Í≤ÄÏÇ¨ Í≤∞Í≥º Î∞ïÏä§ */
.result-box {
    width: 100%;
    height: 350px;
    border: 2px solid #d3d3d3;
    border-radius: 8px;
    padding: 12px;
    font-size: 16px;
    resize: none;
    background-color: white;
    box-sizing: border-box;
}

/* Í≤ÄÏÇ¨ Í≤∞Í≥º Î∞ïÏä§ Ï†ïÎ†¨ Î∞è ÌÅ¨Í∏∞ ÎèôÏùº */
.result-box-container {
    display: flex;
    gap: 10px;
    width: 100%;
}

/* Í≤ÄÏÇ¨ÌïòÍ∏∞ Î≤ÑÌäº ÎÑàÎπÑÎ•º ÏûÖÎ†• Î∞ïÏä§ÏôÄ ÎèôÏùºÌïòÍ≤å */
.check-btn {
    width: 100%;
    height: 50px;
    font-size: 16px;
    font-weight: bold;
    color: white;
    background-color: #026773;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    margin-top: 20px;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
    transition: background-color 0.2s ease-in-out;
}

/* Ïã§ÏãúÍ∞Ñ ÌîºÏã± Ìä∏Î†åÎìú Î≤ÑÌäº */
.trend-btn {
    width: 100%;
    height: 50px;
    font-size: 16px;
    font-weight: bold;
    color: white;
    background-color: #026773;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    margin-top: 20px;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
